import center from '@/views/center'
import adminexam from '@/views/modules/exampaperlist/exam'
    import aboutus from '@/views/modules/aboutus/list'
    import examfailrecord from '@/views/modules/examfailrecord/list'
    import discusskechengziliao from '@/views/modules/discusskechengziliao/list'
    import discussbeikaojingyan from '@/views/modules/discussbeikaojingyan/list'
    import examquestion from '@/views/modules/examquestion/list'
    import exampaper from '@/views/modules/exampaper/list'
    import kaoyanzhengce from '@/views/modules/kaoyanzhengce/list'
    import forum from '@/views/modules/forum/list'
    import yuanxiaoxinxi from '@/views/modules/yuanxiaoxinxi/list'
